<template>
    <div>
        <adt-btns
            :titleNames="titleNames"
            :btnIndex="btnIndex"
            @handleBtn="goToOtherProductList"
            :width="160">
        </adt-btns>
        <adt-search-folder
            :searchRouter="{ query: { siteName: search.siteName } }"
            :isStatus="true"
            :isTitle="false">
            <template slot="status">
                <div class="status">
                    <Col span="12">
                        <div class="name">状态：</div>
                        <span v-for="(status, index) in statusList" :key="index" @click="auditStatus(index)" :class="{'click-status': chosenStatus === index}">
                            {{ status.status }}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <!-- 场所产品 -->
                <Col span="8" class="form-item">
                    <span>场所名称:</span>
                    <Input v-model="search.siteName" clearable />
                </Col>
                <!-- 运输产品 -->
                <!-- <Col span="8" class="form-item">
                    <span>启运地:</span>
                    <Input v-model="search.siteName" clearable />
                </Col>
                <Col span="8" class="form-item">
                    <span>目的地:</span>
                    <Input v-model="search.siteName" clearable />
                </Col> -->
                <!-- 关务产品 -->
                <!-- <Col span="8" class="form-item">
                    <span>报关城市:</span>
                    <Input v-model="search.siteName" clearable />
                </Col> -->
                <!-- 综保区产品 -->
                <!-- <Col span="8" class="form-item">
                    <span>综保区名称:</span>
                    <Input v-model="search.siteName" clearable />
                </Col> -->
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <adt-button :allDelShow="true" :selectList="selectList">
                <Button @click="add()">新建产品<Icon type="ios-add-circle-outline" /></Button>
                <Button @click="setPerference()">设置优惠折扣<Icon type="ios-add-circle-outline" /></Button>
            </adt-button>
            <adt-paginator class="pagination" :currentPage="~~page" :total="~~total" />
        </div>
        <div class="public-component-line"></div>
        <div class="public-list two-column">
            <div class="public-component-card" v-for="(product, id) in listData" :key="id">
                <adt-list-item :data="product" :showCheckbox="true" @checkData="checkData">
                    <template slot="tab-title">
                        <span>产品编号:{{ product.productPlanNumber }}</span>
                    </template>
                    <template slot="tab-operat">
                        <span :class="{'card-status-success': product.statusCode == 'disagree', 'card-status-error': product.statusCode == 'refuse', 'card-status': product.statusCode == 'agree' }">{{ product.status }}</span>
                        <Poptip placement="right-start" class="card-icon">
                            <span>编辑</span>
                            <Icon type="md-arrow-dropdown" />
                            <div class="card-poptip-font" slot="content">
                                <div @click="edit(product.id)">编辑</div>
                                <div @click="detail(product.id)">详情</div>
                                <div @click="remove(product.id)" class="failure">删除</div>
                            </div>
                        </Poptip>
                    </template>
                    <template slot="tab-content">
                        <!-- 场所产品、综保区产品 -->
                        <!-- <div class="tab-content">
                            <div class="province">中国重庆</div>
                            <div class="site">两江寸滩物流堆场</div>
                            <div class="account"><span>90</span>%</div>
                            <svg class="iconfont account-tag" aria-hidden="true">
                                <use xlink:href="#icon-andaotongcaidanICON_huaban28youhuizhekou"></use>
                            </svg>
                        </div> -->
                        <!-- 关务产品 -->
                        <!-- <div class="tab-content">
                            <div class="province">中国重庆</div>
                            <div class="city-task">
                                重庆-<span class="task">（进口报关、出口报关）</span>
                            </div>
                            <div class="account"><span>90</span>%</div>
                            <svg class="iconfont account-tag" aria-hidden="true">
                                <use xlink:href="#icon-andaotongcaidanICON_huaban28youhuizhekou"></use>
                            </svg>
                        </div> -->
                        <!-- 运输产品 -->
                        <div class="tab-content" style="padding-bottom: 10px;">
                            <div class="province">中国重庆</div>
                            <div class="transport-info">
                                <div class="destination-city">重庆</div>
                                <!-- 火车 -->
                                <svg class="iconfont way" aria-hidden="true" v-if="true">
                                    <use xlink:href="#icon-andaotongcaidanICON_huoche"></use>
                                </svg>
                                <!-- 轮船 -->
                                <svg class="iconfont way" aria-hidden="true" v-if="false">
                                    <use xlink:href="#icon-andaotongcaidanICON_lunchuan"></use>
                                </svg>
                                <!-- 汽车 -->
                                <svg class="iconfont way" aria-hidden="true" v-if="false">
                                    <use xlink:href="#icon-huoche"></use>
                                </svg>
                                <div class="type">国际贸易</div>
                                <svg class="iconfont arrow" aria-hidden="true">
                                    <use xlink:href="#icon-andaotongcaidanICON_jiantou"></use>
                                </svg>
                                <div class="dispatch-city">纽约</div>
                            </div>
                            <div class="account"><span>90</span>%</div>
                            <svg class="iconfont account-tag" aria-hidden="true">
                                <use xlink:href="#icon-andaotongcaidanICON_huaban28youhuizhekou"></use>
                            </svg>
                        </div>
                    </template>
                    <template slot="tab-bottom">
                        <div class="tab-bottom">
                            <Row>
                                <Col span="24">服务执行时长：32小时</Col>
                            </Row>
                            <Row>
                                <Col span="12">服务有效期：2020/06/20</Col>
                                <Col span="12">发布价格：￥<span>5200</span>/箱</Col>
                            </Row>
                        </div>
                    </template>
                </adt-list-item>
            </div>
        </div>
        <!-- 设置优惠折扣比modal -->
        <adt-my-modal
            title="设置优惠折扣比"
            :showModal="isModalShow"
            @on-visible-change="visibleChange"
            @on-ok="save()">
            <Form
                ref="formValidate"
                inline
                :model="perferenceData"
                :rules="ruleValidate"
                :label-colon="true"
                label-position="left"
                :label-width="120">
                <FormItem label="优惠折扣比" prop="discount">
                    <Input v-model="perferenceData.discount">
                        <span slot="suffix" style="line-height: 32px;">%</span>
                    </Input>
                </FormItem>
            </Form>
        </adt-my-modal>
        <!-- 删除 -->
        <adt-reconfirm-modal :showModal="isDeleteModalShow" @on-visible-change="deleteVisibleChange" @on-ok="confirmDelete()" />
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
import AdtListItem from '@/components/adt-list-item/Index'
import validateRules from '@/libs/validateRules'
export default {
    mixins: [paginator],
    components: {
        AdtListItem
    },
    data () {
        return {
            titleNames: ['运输产品', '场所产品', '关务产品', '综保区产品'],
            btnIndex: 1,
            search: {},
            isModalShow: false,
            isDeleteModalShow: false,
            perferenceData: {
                discount: ''
            },
            ruleValidate: {
                discount: [
                    validateRules.required('优惠折扣比')[0]
                ]
            },
            selectList: {},
            deleteId: '',
            list: ['加拿大', '北级', '北京', '加州','哈尔滨', '邯郸', '韩国'],
            chosenStatus: '',
            statusList: [
                {code: 'unreviewed', status: '已上载'},
                {code: 'stock', status: '有效'},
                {code: 'progress', status: '失效'}
            ],
            listData: [
                {
                    id: 1,
                    statusCode: 'disagree',
                    status: '需求未同意',
                    logisticsPlanNumber: '物流方案编号1',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            province: '重庆1',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            province: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            province: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    id: 2,
                    statusCode: 'agree',
                    status: '方案已同意',
                    logisticsPlanNumber: '物流方案编号2',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            province: '重庆2',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            province: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            province: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    id: 3,
                    statusCode: 'refuse',
                    status: '已拒绝',
                    logisticsPlanNumber: '物流方案编号3',
                    time: '时效',
                    price: '价格',
                    location: [
                        {
                            province: '重庆3',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            province: '香港',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            province: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                }
            ]
        }
    },
    methods: {
        dataInit () {
            this.getData()
        },
        getData () {
            // api().then(({ data }) => {

            // })
        },
        // 控制状态切换传值
        auditStatus (index) {
            this.chosenStatus = index
        },
        visibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        deleteVisibleChange (changeStatus) {
            this.isDeleteModalShow = changeStatus
        },
        save () {
            this.$refs.formValidate.validate((valid) => {
                if (valid) {
                    const data = this.perferenceData
                    api({ data }).then(({ data }) => {

                    })
                }
            })
        },
        setPerference () {
            this.isModalShow = true
        },
        add () {
            this.$router.push('/product-editor/add-transport-product')
        },
        goToOtherProductList (index) {
            switch (index) {
                case 0:
                    this.$router.push('/product-editor/site-list/1')
                    break
                case 1:
                    this.$router.push('/product-editor/transport-list/1')
                    break
                case 2:
                    this.$router.push('/product-editor/customs-list/1')
                    break
                case 3:
                    this.$router.push('/product-editor/cpa-list/1')
                    break
            }
        },
        checkData (data, status) {
            const curId = data.id
            const ids = this.selectList.ids || []
            // 选中
            if (status) {
                ids.push(curId)
            } else {
                // 取消选中
                const index = ids.indexOf(curId)
                ids.splice(index, 1)
            }
            this.selectList.ids = ids
        },
        edit (id) {
            this.$router.push('')
        },
        detail (id) {
            this.$router.push('')
        },
        remove (id) {
            this.deleteId = id
        },
        confirmDelete () {

        },
    },
}
</script>

<style lang="scss" scoped>
    .tab-content {
        position: relative;
        padding: 0 0 40px 10px;
        .province {
            padding: 10px 0;
            font-size: $font-size-small;
            color: $conventional-font-color;
        }
        .site {
            @include ellipsis();
            max-width: 80%;
            margin-left: 10px;
            font-size: 20px;
        }
        .city-task {
            display: flex;
            align-items: center;
            margin-left: 10px;
            font-size: 20px;
            .task {
                @include ellipsis();
                display: inline-block;
                max-width: 80%;
                color: $secondary-font-color;
            }
        }
        .transport-info {
            display: flex;
            align-items: center;
            margin: -10px 0 0 10px;
            font-size: 20px;
            .way {
                margin-left: 12px;;
                color: $warning-color;
                font-size: 24px;
            }
            .type {
                color: $light-color;
                font-size: 12px;
                transform: scale(.8);
            }
            .arrow {
                margin-right: 12px;
                color: $light-color;
                font-size: 70px;
            }
        }
        .account {
            position: absolute;
            top: 12px;
            right: 18px;
            span {
                font-size: 28px;
            }
        }
        .account-tag {
            position: absolute;
            top: -8px;
            right: -7px;
            color: $light-color;
            font-size: 60px;
        }
    }
</style>
