export default {
    props: {
        page: { default: 1 }
    },
    data () {
        return {
            total: 0,
            perpage: 15
        }
    },
    mounted () {
        this.dataInit()
    },
    watch: {
        $route () {
            this.dataInit()
        }
    },
    methods: {
        paginatorInit (perpage, total) {
            this.perpage = perpage
            this.total = total
        }
    }
}
